header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

h1:hover {
    color: var(--color-primary-variant);
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    font-size: 1.5rem;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 1rem;
    background-color: var(--color-primary);
}

/** image */
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 26rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 13rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
}

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
}
/** ===== MEDIA QUERIES ===== **/
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header__socials,
    .scroll__down {
        display: none;
    }
}
